.header {
    background-color: #001a3b;
    
    *:not(.btn) {color: white};
    
    &__wrapper {
        display: grid;
        grid-template-columns: auto auto auto;
        grid-row-gap: 2rem;
        padding: 2.4rem 2rem 3rem 2rem;
    }
    
    &__logo {
        grid-column: 1 / 6;
        grid-row: 1 / 1;
        
        img {
            width: 100%;
            height: 90px;
            object-fit: contain;
        }
    }
    
    &__nav {
        grid-column: 1 / 5;
        grid-row: 2 / 2;
        
        nav ul {
            list-style: none;
            
            li {
                display: inline-block;
                padding: 0 .8rem;
                font-size: 1.2rem;
            }
        }
    }
    
    &__append {
        grid-column: 5 / 6;
        grid-row: 2 / 2;
    }
}